.container {
  margin: var(--space-md) auto;
  max-width: 95%;
}
@media (min-width: 62.5em) {
  .container {
    max-width: 950px;
  }
}
@media (min-width: 78.125em) {
  .container {
    max-width: 1200px;
  }
}

.pos-rlt {
  position: relative;
}

.pos-abs {
  position: absolute;
}

.center-text {
  text-align: center;
}

.row {
  --columns: 4;
}

.ft-style {
  color: var(--ft-clr);
  text-align: center;
  font-size: calc(var(--fs-sm) / 1.2);
}

:root {
  /* Violet */
  --violet-100: #666ca3;
  --violet-200: #4851ff;
  --violet-300: #13183f;
  --violet-400: #f0f1ff;
  /* Gray */
  --gray-100: #ffffff;
  /* Crimson */
  --crimson-100: #f02aa6;
  /* Scarlet red */
  --scarletRed-100: #ffa7c3;
  --scarletRed-200: #eb0058;
  /* Blue violet */
  --blueViolet-100: #72758c;
  /* Vermilion */
  --vermilion-100: #ff6f48;
  --color-text: var(--blueViolet-100);
  --color-heading-1: var(--violet-300);
  --color-heading-2: var(--gray-100);
  --color-link: var(--scarletRed-200);
  --color-link-hover: var(--scarletRed-100);
  --color-btn: var(--gray-100);
  --btn-secondary-bg: var(--violet-300);
  --btn-hover: var(--scarletRed-100);
  --btn-secondary-hover: var(--violet-100);
  --card-bg: var(--gray-100);
  --footer-bg: var(--violet-300);
  --ft-clr: var(--scarletRed-200);
}
:root body {
  color: var(--color-text);
}
:root h1,
:root h3 {
  color: var(--color-heading-1);
}
:root h2 {
  color: var(--color-heading-2);
}
:root .link {
  color: var(--color-link);
}
:root .link:hover {
  color: var(--color-link-hover);
}

:root {
  --font-primary: "Plus Jakarta Sans", sans-serif;
  --fw-500: 500;
  --fw-700: 700;
  --fw-800: 800;
  --fw-small: var(--fw-500);
  --fw-medium: var(--fw-700);
  --fw-large: var(--fw-800);
}

body {
  font-family: var(--font-primary);
  font-weight: var(--fw-small);
}

h1,
h2,
h3 {
  font-weight: var(--fw-large);
}

a,
button {
  font-weight: var(--fw-medium);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  background: linear-gradient(180deg, var(--gray-100) 0%, var(--violet-400) 100%);
  line-height: 1.5;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

a {
  text-decoration: none;
}
a:focus, a:visited {
  color: unset;
}

img,
picture {
  max-width: 100%;
  display: block;
}

ul,
ol {
  list-style: none;
}

button,
input,
select,
textarea {
  font: inherit;
  outline: none;
  background-color: transparent;
  border: none;
}

:root {
  --fs-sm: 1rem;
  --fs-md: 1.125rem;
  --fs-lg: 1.25rem;
  --fs-xl: 1.5rem;
  --fs-xxl: 1.75rem;
  --fs-xxxl: 2.5rem;
  /* spacing values */
  --space-xxxs: 0.25em;
  --space-xxs: 0.375em;
  --space-xs: 0.5em;
  --space-sm: 0.75em;
  --space-md: 1.25em;
  --space-lg: 2em;
  --space-xl: 3.25em;
  --space-xxl: 5.25em;
  --space-xxxl: 8.5em;
  --text-size: var(--fs-sm);
  --heading-size: var(--fs-xxxl);
  --subHeading-size: var(--fs-xl);
  --subtitle-size: var(--fs-lg);
  --link-size: var(--fs-md);
  --fs-btn: var(--fs-sm);
}
@media (min-width: 78.125em) {
  :root {
    --fs-sm: 1.125rem;
    --fs-lg: 1.5rem;
    --fs-xl: 2rem;
    --fs-xxxl: 3.5rem;
  }
}

body {
  font-size: var(--text-size);
}

h1 {
  font-size: var(--heading-size);
}

h2 {
  font-size: var(--subHeading-size);
}

h3 {
  font-size: var(--subtitle-size);
}

a {
  font-size: var(--link-size);
}

.btn {
  font-size: var(--fs-btn);
}

:root {
  --columns: 12;
  --width: 12;
  --constantGap: 0.1%;
}

.row {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

[class*=col-] {
  --initialBasis: calc(var(--width, 0) / var(--columns, 16) * 100%);
  --gap: calc((var(--columns) - var(--width)) * var(--constantGap));
  flex-basis: calc(var(--initialBasis) - var(--gap));
}

.btn {
  color: var(--color-btn);
  padding: 0.6875rem 1.5rem;
  border-radius: 20px;
  text-transform: capitalize;
  text-align: center;
  white-space: nowrap;
  line-height: 1;
  position: relative;
  max-width: 187px;
  z-index: 1;
}
.btn:visited, .btn:focus, .btn:hover {
  color: var(--color-btn);
  transition: all 0.25s ease-in;
}
.btn.btn-primary-bg::after, .btn.btn-accent-bg::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--btn-hover);
  z-index: -1;
  border-radius: 20px;
  opacity: 0;
  transition: all 0.25s ease-in;
}
.btn.btn-primary-bg:hover::after, .btn.btn-accent-bg:hover::after {
  opacity: 1;
}
.btn.btn-primary-bg {
  background: linear-gradient(180deg, var(--vermilion-100) 0%, var(--crimson-100) 100%);
}
.btn.btn-secondary-bg {
  background-color: var(--btn-secondary-bg);
}
.btn.btn-secondary-bg:hover {
  background-color: var(--btn-secondary-hover);
}
.btn.btn-accent-bg {
  background: linear-gradient(180deg, var(--violet-200) 0%, var(--crimson-100) 100%);
}

.card-white {
  background-color: var(--card-bg);
}

.card-colored {
  background: linear-gradient(180deg, var(--vermilion-100) 0%, var(--crimson-100) 100%);
}

.cards {
  justify-content: space-between;
}

.card {
  padding: var(--space-lg) var(--space-md);
  margin-bottom: var(--space-xl);
  border-radius: 20px;
  max-height: 16.1875rem;
}
.card__icon {
  top: -15%;
}
.card__title, .card__desc {
  margin-bottom: var(--space-sm);
}
.card__desc {
  max-width: 95%;
}

[class*=col-3-] {
  --width: 4;
}

@media (min-width: 48em) {
  .card {
    height: 16.1875rem;
  }
  [class*=col-3-] {
    --constantGap: 0.5%;
    --width: 2;
  }
}
@media (min-width: 62.5em) {
  [class*=col-3-] {
    --constantGap: 0.1%;
    --width: 4;
  }
}
.header__wrapper {
  justify-content: space-between;
}
@media (min-width: 48em) {
  .header__wrapper:last-child {
    padding-block: var(--space-xxl);
  }
}
.header__title {
  margin: 0 0 var(--space-xxxs) 0;
  letter-spacing: 5px;
}
.header__desc {
  margin: 0 0 var(--space-lg) 0;
  max-width: 90%;
  letter-spacing: 2px;
}
.header__img {
  background-image: url("../../dist/images/image-hero-mobile.png");
  background-image: image-set(url("../../dist/images/image-hero-mobile.webp"));
  width: 100%;
  height: 30em;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 48em) {
  .header__img {
    position: absolute;
    left: 0;
    top: 5px;
    background-image: url(../../dist/images/image-hero-tablet.png);
    background-image: image-set(url(../../dist/images/image-hero-tablet.webp));
    height: 32em;
    background-position: top right;
  }
}
@media (min-width: 62.5em) {
  .header__img {
    background-image: url(../../dist/images/image-hero-desktop.png);
    background-image: image-set(url(../../dist/images/image-hero-desktop.webp));
    background-position: top -4px right -30px;
  }
}
@media (min-width: 120em) {
  .header__img {
    position: unset;
    left: 0;
    top: 0;
  }
}

.place-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

[class*=col-1-] {
  --width: 2;
}

[class*=col-2-] {
  --width: 4;
}

@media (min-width: 48em) {
  [class*=col-2-] {
    --width: 2;
  }
}
@media (min-width: 62.5em) {
  .row {
    --columns: 12;
    --constantGap: 0.1%;
  }
  [class*=col-1-] {
    --width: 6;
  }
  [class*=col-2-] {
    --width: 6;
  }
}
.footer-bg {
  background-color: var(--footer-bg);
}

.footer__wrapper {
  margin-bottom: 0;
  justify-content: space-between;
  padding-block: var(--space-lg);
}

[class*=col-4-] {
  --width: 2;
}

@media (min-width: 62.5em) {
  .row {
    --columns: 12;
  }
  [class*=col-4-] {
    --width: 6;
  }
}